import React, {Component} from 'react';
import { Link } from "react-router-dom";
import logoscGray from '../images/logo-sc-gray.svg';



class Footer extends Component {

    render(){
        return (
                <div className="mastfoot">

                    <div className="h-bg-color-athens-gray  section">
                        <div className="row section__inner">
                            <div className="nav-footer  small-24  columns">
                                <div className="nav-footer__inner">
                                    <div className="row">
                                        <a className="nav-footer__logo small-24 large-6  columns" href="index.html">
                                            <img src={logoscGray} alt={"Security Choice Logo"}/>
                                        </a>
                                        <div className="nav-footer__right-column medium-24 large-18  columns">
                                            <ul id="menu-mastfoot" className="nav-footer__menu h-float-left">
                                                <li className="nav-footer__menu-item menu-item menu-item-type-post_type menu-item-object-page"><Link to="/about">Packages</Link></li>
                                                <li className="nav-footer__menu-item menu-item menu-item-type-post_type menu-item-object-page"><Link to="/about">Equipment</Link></li>
                                                <li className="nav-footer__menu-item menu-item menu-item-type-custom menu-item-object-custom"><Link to="/about">Local</Link></li>
                                                <li className="nav-footer__menu-item menu-item menu-item-type-post_type menu-item-object-page"><Link to="/about">Resources</Link></li>
                                            </ul>
                                            <ul className="nav-footer__menu-legal">
                                                <li className="nav-footer__menu-item menu-item menu-item-type-post_type menu-item-object-page"><Link to="/terms">Terms &#038; Conditions</Link></li>
                                                <li className="nav-footer__menu-item menu-item menu-item-type-post_type menu-item-object-page"><Link to="/privacy">Privacy</Link></li>
                                            </ul>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="row section__inner">
                            <div className="columns" id="terms-conditions-start">
                                <p className="legal">&dagger; With $99.00 Customer Installation Charge and purchase of alarm monitoring services. Termination Fee applies. For New Customers only. See Important Terms and Conditions to this offer below [or on reverse side when applicable].”Legal Disclaimer:$99 Customer Installation Charge. 36-Month Monitoring Agreement required at $36.99 per month ($1,331.64). 24-Month Monitoring Agreement required at $36.99 ($887.76) for California, including Quality Service Plan (QSP). Form of payment must be by credit card or electronic charge to your checking or savings account. Offer applies to homeowners only. Local permit fees may be required. Satisfactory credit history required. Termination Fee applies. Certain restrictions may apply. Offer valid for new ADT Authorized Dealer customers only and not on purchases from ADT LLC. Other rate plans available. Cannot be combined with any other offer.</p>
                                <p className="legal">** Restrictions apply. Visa gift cards issued by MetaBank™, Member FDIC, pursuant to a license from Visa U.S.A Inc. Neither MetaBank nor Visa is a sponsor, endorser or participant in this promotion. Monthly inactivity fee of $2.95 after 12 consecutive months of inactivity. Lost/stolen replacement card fee of $5.00.</p>
                                <p className="legal">^ Certain restrictions apply.</p>
                                <p className="legal">ADT Pulse Interactive Solutions Services, which help you manage your home environment and family lifestyle, requires the purchase and/or activation of an ADT alarm system with monitored burglary service and a compatible computer, cell phone or PDA with Internet and email access. These ADT Pulse Interactive Solutions Services do not cover the operation or maintenance of any household equipment/systems that are connected to the ADT Pulse Interactive Solutions Services/Equipment. All ADT Pulse Interactive Solutions Services are not available with the various levels of ADT Pulse Interactive Solutions Services. All ADT Pulse Interactive Solutions Services may not be available in all geographic areas. Standard message and data rates may apply to text alerts. You may be required to pay additional charges to purchase equipment required to utilize the ADT Pulse Interactive Solutions Services features you desire.</p>
                                <p className="legal">Installation starts at $199. Burglary, Fire, Carbon Monoxide and Medical Alert monitoring requires purchase and/or activation of an ADT security system with monitored Burglary, Fire, Carbon Monoxide and Medical Alert devices. Fire, Carbon Monoxide and Medical Alert services are an additional charge. Quality Service Plan (QSP) is ADT’s Extended Limited Warranty. 36-month monitoring contract required from $49.99 per month, ($1,871.64), including Quality Service Plan (QSP). Additional charges may apply in areas that require guard response service for municipal alarm verification. Prices subject to change. Prices may vary by market. Some insurance companies offer discounts on Homeowner’s Insurance. Please consult your insurance company. Local permit fees may be required. Satisfactory credit history required. Additional monitoring fees required for some services. Photos are for illustrative purposes only and may not reflect the exact product/service actually provided.</p>
                                <p className="legal">Actual system may vary from item pictured.</p>
                            </div>
                        </div>
                    </div>

                </div>
        )
    }
}
export default Footer;