import React, {Component} from 'react';
import { Link } from "react-router-dom";

import productVrc from "../images/product-vrc-card.png";
import iconAlarmbell from "../images/icon-alarmbell.svg";
import iconmonitoringStaff from "../images/icon-monitoring-staff.svg";
import iconPiggybank from "../images/icon-piggybank.svg";

import Slider from "react-slick";

class HomePage extends Component {
    render(){
        var settings = {
            dots: false,
            infinite: true,
            slidesToShow: 1,
            slidesToScroll: 1,
            autoplay: true,
            autoplaySpeed: 2000,
            arrows: false
        };

        return (
            <div>
                <section className="regular slider">
                    <Slider {...settings}>
                        <div className="hero hero--landing section">
                        <div className="row section__inner">
                            <div className="small-24  large-15 columns  h-text-align-center">
                                <h1 className="hero__headline">Protect your home with PYMOT Security systems</h1>
                                <div className="hero__wrap">
                                    <p className="hero__wrap-text">Monitored home security packages starting at <font size="+3">$9</font> per week*</p>
                                    <div className="hero__button-container">
                                        <Link to="/terms" className="button button__clear button__clear--white button__clear--hero  h-nowrap">View Packages</Link>
                                    </div>
                                </div>
                               {/* <button className="button button__secondary  hero__button  h-margin-bottom-30 h-nowrap" data-open="free-quote-modal">Get a FREE Quote</button>*/}
                                <p className="legal legal__hero">*$99.00 Customer Installation Charge. 36-month monitoring contract required. Termination fees apply. See&nbsp;
                                    <Link to="/terms" className="hero__legal-link  h-underline h-nowrap">terms &amp; conditions</Link>.
                                </p>
                            </div>
                        </div>
                        </div>
                        <div className="hero hero--landing--2 section">
                        <div className="row section__inner">
                            <div className="small-24  large-15 columns  h-text-align-center">
                                <h1 className="hero__headline">Help protect your home with ADT Security systems</h1>
                                <div className="hero__wrap">
                                    <p className="hero__wrap-text">Monitored home security packages starting at <font size="+3">$9</font> per week*</p>
                                    <div className="hero__button-container">
                                        <Link to="/terms" className="button button__clear button__clear--white button__clear--hero  h-nowrap">View Packages</Link>
                                    </div>
                                </div>
                               {/* <button className="button button__secondary  hero__button  h-margin-bottom-30 h-nowrap" data-open="free-quote-modal">Get a FREE Quote</button>*/}
                                <p className="legal legal__hero">*$99.00 Customer Installation Charge. 36-month monitoring contract required. Termination fees apply. See
                                    <Link to="/terms" className="hero__legal-link  h-underline h-nowrap">terms &amp; conditions</Link>.
                                </p>
                            </div>
                        </div>
                        </div>
                        <div className="hero hero--landing--3 section">
                            <div className="row section__inner">
                                <div className="small-24  large-15 columns  h-text-align-center">
                                    <h1 className="hero__headline">Help protect your home with ADT Security systems</h1>
                                    <div className="hero__wrap">
                                        <p className="hero__wrap-text">Monitored home security packages starting at <font size="+3">$9</font> per week*</p>
                                        <div className="hero__button-container">
                                            <Link to="/terms" className="button button__clear button__clear--white button__clear--hero  h-nowrap">View Packages</Link>
                                        </div>
                                    </div>
                                    {/* <button className="button button__secondary  hero__button  h-margin-bottom-30 h-nowrap" data-open="free-quote-modal">Get a FREE Quote</button>*/}
                                    <p className="legal legal__hero">*$99.00 Customer Installation Charge. 36-month monitoring contract required. Termination fees apply. See
                                        <Link to="/terms" className="hero__legal-link  h-underline h-nowrap">terms &amp; conditions</Link>.
                                    </p>
                                </div>
                            </div>
                        </div>
                    </Slider>
                </section>
                <section className="banner banner--hero">
                    <div className="row  columns">
                        <h4 className="banner__text">Find an PYMOT monitored home security system that fits you best. <span className="h-nowrap"><strong>Call us today</strong></span>.</h4>
                    </div>
                </section>
                <section className="section home-vrc">
                    <div className="section__inner">
                        <div className="row">
                            <div className="small-24 medium-24 large-14 columns home-vrc__inner-content">
                                <h2>Order today and get 25% Off <sup>**</sup></h2>
                                <h3 className="home-vrc__subheader">Call Now for a FREE Quote</h3>
                                <p className="home-vrc__phone show-for-medium" data-fuse data-fuse-format="1-###-###-####"></p>
                                <a className="button button__secondary  show-for-small-only  h-center-element h-margin-bottom-20" href="tel:" data-fuse data-fuse-format="Call 1-###-###-####">
                                    Call 1-855-511-3204 now or
                                </a>
                                <p className="legal">**With system installation and Visa<sup>&reg;</sup> Reward Card provided by SafeStreets USA. See <Link to="/terms" className="h-underline">terms conditions</Link>.</p>
                            </div>
                            <img className="home-vrc__vrc-image" src={productVrc} alt={"VRC Card"}/>
                        </div>
                    </div>
                </section>
                <section className="card  home-value-pts  section">
                    <div className="row">
                        <div className="columns small-24">
                            <h2 className="card__section-header h-text-align-center h-text-color-blue">Get peace of mind with ADT monitoring</h2>
                        </div>
                    </div>
                    <div className="row align-center  js-slick-card-slider">
                        <div className="medium-22 large-8 columns">
                            <div className="card__container js-card height_card__container">
                                <div className="card__icon-container  home-value-pts__icon-container">
                                    <img className="home-value-pts__icon" src={iconAlarmbell } alt={"alarmbell ringing icon"}/>
                                </div>
                                <div className="card__content-container">
                                    <h4 className="card__header  h-line-height">Control</h4>
                                    <p className="card__content">
                                        Any standard electrical devices (lights, fans, door locks etc) or high capacity devices (water heaters, pumps, motors etc.) can be controlled either locally or as 'Internet of Things’ devices.Our solution allows control of 0-10V variations needed for dimming or speed control and it supports directional control as needed for curtains or projection screens.
                                    </p>
                                </div>
                            </div>
                        </div>
                        <div className="medium-22 large-8 columns">
                            <div className="card__container js-card height_card__container">
                                <div className="card__icon-container  home-value-pts__icon-container">
                                    <img className="home-value-pts__icon--margin" src={iconmonitoringStaff} alt={"monitoring staff wearing headsets icon"}/>
                                </div>
                                <div className="card__content-container">
                                    <h4 className="card__header">Sense</h4>
                                    <p className="card__content card__content--margin-top">
                                        Receive notifications on Smart Apps or locally from the triggering of any sensors (NO/NC sensors such as smoke, gas leak, door/shutter intrusion, glass break, PIR-motion, beam detectors).
                                        Monitor data generated from analog sensors (temperature, humidity etc.) or limit sensors (water/liquid tank levels).
                                    </p>
                                </div>
                            </div>
                        </div>
                        <div className="medium-22 large-8 columns">
                            <div className="card__container js-card height_card__container">
                                <div className="card__icon-container  home-value-pts__icon-container">
                                    <img className="home-value-pts__icon" src={iconPiggybank} alt="piggybank savings icon"/>
                                </div>
                                <div className="card__content-container">
                                    <h4 className="card__header">Centralize</h4>
                                    <p className="card__content card__content--margin-top">
                                        Our software technology offers a scalable, centralized management system for buildings and campuses to control, sense and monitor any electrical devices or sensors within them via our IoT nodes, sensors and gateways.
                                        The software can be made accessible via kiosks, desktops or Smart Apps. It can be deployed on local networks or cloud-enabled.
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
                <section className="banner banner--gafq  banner--adt-background">
                    <div className="row columns">
                        <h2 className="banner__headline banner__headline--gafq">Help protect your home today.</h2>
                        <p className="banner__content  banner__content--gafq">One of our monitoring professionals can get you set up with a monitored home security system for you and your family.</p>
                        <button className="button button__primary" data-open="free-quote-modal">
                            Get a FREE Quote
                        </button>
                    </div>
                </section>
                <section className="section h-padding-bottom-50">
                    <div className="section__inner">
                        <h2 className="h-margin-bottom-20 h-text-align-center h-text-color-blue">Compare Packages</h2>
                        <p className="section__subhead">From just the essentials to premium protection, there are ADT monitoring packages for every home. Pick yours now.</p>
                    </div>
                    <div className="card-package-container row js-slick-card-slider">
                        <div className="card-package small-24 medium-12 large-8 columns">
                            <div className="card-package__inner" data-height="match-height-container">
                                <div className="card-package__top" data-height="match-height-item">
                                    <h3 className="card-package__header">Essentials</h3>
                                </div>
                                <div className="card-package__bottom" data-height="match-height-item">
                                    <p className="card-package__subheader h-text-align-center">Includes basic features and optional upgrades</p>
                                    <ul className="card-package__list">
                                        <li className="card-package__list-item">24/7 ADT monitoring</li>
                                        <li className="card-package__list-item">Digital Keypad</li>
                                        <li className="card-package__list-item card-package__list-item--no-icon">and much more…</li>
                                    </ul>
                                    <button className="card-package__button  button button__clear button__clear--blue h-center-element h-margin-top-30" data-open="free-quote-modal">Get a FREE Quote</button>
                                </div>
                            </div>
                        </div>
                        <div className="card-package small-24 medium-12 large-8 columns">
                            <div className="card-package__inner" data-height="match-height-container">
                                <div className="card-package__top" data-height="match-height-item">
                                    <h3 className="card-package__header">Total Protection</h3>
                                </div>
                                {/*<!-- /.card-packages__top-->*/}
                                <div className="card-package__bottom" data-height="match-height-item">
                                    <p className="card-package__subheader h-text-align-center">Everything in the Essentials<br/>package</p>
                                    <p className="card-package__divider">-PLUS-</p>
                                    <ul className="card-package__list">
                                        <li className="card-package__list-item">Safewatch<sup>&reg;</sup> CellGuard<sup>&reg;</sup></li>
                                        <li className="card-package__list-item">No landline necessary</li>
                                    </ul>
                                    <button className="card-package__button  button button__clear button__clear--blue h-center-element h-margin-top-30" data-open="free-quote-modal">Get a FREE Quote</button>
                                </div>
                            </div>
                        </div>
                        <div className="card-package small-24 medium-12 medium-offset-6 large-8 large-offset-0 columns">
                            <div className="card-package__inner" data-height="match-height-container">
                                <div className="card-package__top" data-height="match-height-item">
                                    <h3 className="card-package__header">Premium Protection <span className="card-package__header--small">with ADT Pulse</span></h3>
                                </div>
                                <div className="card-package__bottom" data-height="match-height-item">
                                    <p className="card-package__subheader h-text-align-center">Everything in the Total Protection package</p>
                                    <p className="card-package__divider">-PLUS-</p>
                                    <ul className="card-package__list">
                                        <li className="card-package__list-item">ADT Pulse<sup>&reg;</sup> mobile access</li>
                                    </ul>
                                    <button className="card-package__button  button button__clear button__clear--blue h-center-element h-margin-top-30" data-open="free-quote-modal">Get a FREE Quote</button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="section__inner h-margin-top-20">
                        <Link to="/about" className="button button__primary h-center-element h-margin-bottom-40">Compare Our Packages</Link>
                        <p className="legal h-text-align-center">*Additional charges may apply. See terms and conditions <Link to="/terms" className="h-underline">here</Link>.</p>
                    </div>
                </section>
            </div>

        )
    }
}
export default HomePage;