import React, {Component} from 'react';

import buildingmanagementSystem from "../images/building-management-system.jpg";

class ContactPage extends Component {

    render(){
        return (
            <div>
                <section className="regular slider" role="banner">
                    <div className="hero hero--equipment hero--secondary">
                        <div className="row">
                            <div className="small-24 large-14 columns  h-text-align-center">
                                <h1 className="hero__headline  h-margin-bottom-10">ADT Monitoring Equipment and Features</h1>
                                <p className="hero__wrap-text">Help secure your home with top technology</p>
                                <button className="button button__primary  h-margin-bottom-30" data-open="free-quote-modal">
                                    Get a FREE Quote
                                </button>
                            </div>
                        </div>
                    </div>
                </section>

                <section className="card  home-value-pts section">
                    <div className="row">
                        <div className="columns small-24">
                            <h2 className="card__section-header h-text-align-left h-text-color-blue">News</h2>
                        </div>
                    </div>
                    <div className="row align-center  js-slick-card-slider">
                        <div className="medium-24 large-16 columns">
                            <div className="card__container js-card">
                                <div className="row h-text-align-left">
                                    <div className="medium-10 large-10 columns">
                                        <img src={buildingmanagementSystem} alt={"Building Management System"}/>
                                    </div>
                                    <div className="medium-14 large-14 columns">
                                        <div className="card__content-container">
                                            <p className="card__content h-text-align-left h-font-size-20"><strong>America’s #1 Monitored Home Security</strong></p>
                                            <p className="card__content h-text-align-left">
                                                BuildTrack is a leader in Smart Automation for Homes, Enterprises, Hotels, Hospitals, Senior Living, Energy Efficiency and Building Management Systems (BMS).
                                            </p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="card__container js-card">
                                <div className="row h-text-align-left">
                                    <div className="medium-10 large-10 columns">
                                        <img src={buildingmanagementSystem} alt={"Building Management System"}/>
                                    </div>
                                    <div className="medium-14 large-14 columns">
                                        <div className="card__content-container">
                                            <p className="card__content h-text-align-left h-font-size-20"><strong>America’s #1 Monitored Home Security</strong></p>
                                            <p className="card__content h-text-align-left">
                                                BuildTrack is a leader in Smart Automation for Homes, Enterprises, Hotels, Hospitals, Senior Living, Energy Efficiency and Building Management Systems (BMS).
                                            </p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="card__container js-card">
                                <div className="row h-text-align-left">
                                    <div className="medium-10 large-10 columns">
                                        <img src={buildingmanagementSystem} alt={"Building Management System"}/>
                                    </div>
                                    <div className="medium-14 large-14 columns">
                                        <div className="card__content-container">
                                            <p className="card__content h-text-align-left h-font-size-20"><strong>America’s #1 Monitored Home Security</strong></p>
                                            <p className="card__content h-text-align-left">
                                                BuildTrack is a leader in Smart Automation for Homes, Enterprises, Hotels, Hospitals, Senior Living, Energy Efficiency and Building Management Systems (BMS).
                                            </p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="medium-24 large-8 columns">
                            <div className="card__container js-card">
                                <div className="card__content-container">
                                    <p className="card__header h-text-align-left">Recent News</p>
                                    <p className="card__content card__content--margin-top h-text-align-left">If you’re dissatisfied with your ADT monitored home alarm system and the company has tried its within six months</p>
                                    <p className="legal h-margin-top-10 h-text-align-left">Certain restrictions may apply.</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        )
    }
}
export default ContactPage;